MapObj.formData = function (points, width) {
    const source = points.split(',')
    const result = []
    source.forEach((point, index) => {
        const mod = index % Number(width)
        if (mod === 0) {
            result[result.length] = []
        }
        result[result.length - 1].push(Number(point))
    })
    return result
}

export default MapObj
